const H1 = styled.h1`color:inherit;`
const H2 = styled.h2`color:inherit;`
const H3 = styled.h3`color:inherit;`
const H4 = styled.h4`color:inherit;`
const H5 = styled.h5`color:inherit;`
const H6 = styled.h6`color:inherit;`
const HDiv = styled.div`color:inherit;`
const Heading = (props) => {
    // switch (props.level) {
    //     case 1: return styled.h1``
    //     case 2: return styled.h2``
    //     case 3: return styled.h3``
    //     case 4: return styled.h4``
    //     case 5: return styled.h5``
    //     case 6: return styled.h6``
    //     default: return styled.h1``
    // }
    if (props.h1) return <H1 {...props} />
    if (props.h2) return <H2 {...props} />
    if (props.h3) return <H3 {...props} />
    if (props.h4) return <H4 {...props} />
    if (props.h5) return <H5 {...props} />
    if (props.h6) return <H6 {...props} />
    return <HDiv {...props} />
}
